<template>
  <div class="page-body" v-if="order.order_id">
    <van-nav-bar
      fixed
      title="订单详情"
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <div class="header">
      <!-- 订单状态 -->
      <div class="order-status">
        <div class="status-icon">
          <!-- 进行中的订单 -->
          <span v-if="order.order_status == 10">
            <!-- 待支付 -->
            <span v-if="order.pay_status == 10">
              <img class="image" src="@/assets/order/status/wait_pay.png" />
            </span>
            <!-- 待发货 -->
            <span v-else-if="order.delivery_status == 10">
              <img class="image" src="@/assets/order/status/wait_deliver.png" />
            </span>
            <!-- 待收货 -->
            <span v-else-if="order.receipt_status == 10">
              <img class="image" src="@/assets/order/status/wait_receipt.png" />
            </span>
          </span>

          <!-- 已完成 -->
          <span v-if="order.order_status == 30">
            <img class="image" src="@/assets/order/status/received.png" />
          </span>

          <!-- 已取消/待取消 -->
          <span v-if="order.order_status == 20 || order.order_status == 21">
            <img class="image" src="@/assets/order/status/close.png" />
          </span>
        </div>
        <div class="status-text">
          <span>{{ order.state_text }}</span>
        </div>
      </div>

      <!-- 下一步操作 -->
      <div class="next-action" v-if="order.order_status == 10">
        <div
          v-if="order.pay_status == 10"
          class="action-btn"
          @click="isPayAction = true"
        >
          去支付
        </div>
        <div
          v-if="order.delivery_status == 20 && order.receipt_status == 10"
          class="action-btn"
          @click="toConfirmReceipt(order.order_id)"
        >
          确认收货
        </div>
      </div>
    </div>

    <!-- 快递配送：配送地址 -->
    <div class="delivery-address i-card">
      <div class="link-man">
        <span class="name">{{ order.address.name }}</span>
        <span class="phone">{{ order.address.phone }}</span>
      </div>
      <div class="address">
        <span
          class="region"
          v-for="(region, idx) in order.address.region"
          :key="idx"
          >{{ region }}</span
        >
        <span class="detail">{{ order.address.detail }}</span>
      </div>
    </div>

    <!-- 物流信息 -->
    <div
      v-if="
        order.delivery_type == 10 &&
        order.delivery_status == 20 &&
        order.express
      "
      class="express i-card"
      @click="handleTargetExpress()"
    >
      <div class="main">
        <div class="info-item">
          <div class="item-lable">物流公司</div>
          <div class="item-content">
            <span>{{ order.express.express_name }}</span>
          </div>
        </div>
        <div class="info-item">
          <div class="item-lable">物流单号</div>
          <div class="item-content">
            <span>{{ order.express_no }}</span>
            <div class="act-copy" @click="handleCopy(order.express_no)">
              <span>复制</span>
            </div>
          </div>
        </div>
      </div>
      <div class="right-arrow">
        <span class="iconfont icon-arrow-right"></span>
      </div>
    </div>

    <!-- 商品列表 -->
    <div class="goods-list i-card">
      <div class="goods-item" v-for="(goods, idx) in order.goods" :key="idx">
        <div
          class="goods-main"
          @click="$router.push(`/detail/${goods.goods_id}`)"
        >
          <!-- 商品图片 -->
          <div class="goods-image">
            <img class="image" :src="goods.goods_image" />
          </div>
          <!-- 商品信息 -->
          <div class="goods-content">
            <div class="goods-title">
              <span class="twoline-hide">{{ goods.goods_name }}</span>
            </div>
            <div v-if="goods.goods_props" class="goods-props clearfix">
              <div
                class="goods-props-item"
                v-for="(props, idx) in goods.goods_props"
                :key="idx"
              >
                <span>{{ props.value.name }}</span>
              </div>
            </div>
          </div>
          <!-- 交易信息 -->
          <div class="goods-trade">
            <div class="goods-price">
              <span class="unit">￥</span>
              <span class="value">{{
                goods.is_user_grade
                  ? goods.grade_goods_price
                  : goods.goods_price
              }}</span>
            </div>
            <div class="goods-num">
              <span>×{{ goods.total_num }}</span>
            </div>
          </div>
        </div>

        <!-- 商品售后 -->
        <div class="goods-refund">
          <span v-if="goods.refund" class="stata-text">已申请售后</span>
          <div
            v-else-if="order.isAllowRefund"
            class="action-btn"
            @click="
              $router.push(`/applyrefund?orderGoodsId=${goods.order_goods_id}`)
            "
          >
            申请售后
          </div>
        </div>
      </div>
    </div>

    <!-- 订单信息 -->
    <div class="order-info i-card">
      <div class="info-item">
        <div class="item-lable">订单编号</div>
        <div class="item-content">
          <span>{{ order.order_no }}</span>
          <div class="act-copy" @click="handleCopy(order.order_no)">
            <span>复制</span>
          </div>
        </div>
      </div>
      <div class="info-item">
        <div class="item-lable">下单时间</div>
        <div class="item-content">
          <span>{{ order.create_time }}</span>
        </div>
      </div>
      <div class="info-item">
        <div class="item-lable">买家留言</div>
        <div class="item-content">
          <span>{{ order.buyer_remark ? order.buyer_remark : "--" }}</span>
        </div>
      </div>
    </div>

    <!-- 结算信息 -->
    <div class="trade-info i-card">
      <div class="info-item">
        <div class="item-lable">订单金额</div>
        <div class="item-content">
          <span>￥{{ order.total_price }}</span>
        </div>
      </div>
      <div v-if="order.coupon_money > 0" class="info-item">
        <div class="item-lable">优惠券抵扣</div>
        <div class="item-content">
          <span>-￥{{ order.coupon_money }}</span>
        </div>
      </div>
      <div v-if="order.points_money > 0" class="info-item">
        <div class="item-lable">{{ setting.points_name }}抵扣</div>
        <div class="item-content">
          <span>-￥{{ order.points_money }}</span>
        </div>
      </div>
      <div class="info-item">
        <div class="item-lable">运费</div>
        <div class="item-content">
          <span>+￥{{ order.express_price }}</span>
        </div>
      </div>
      <div v-if="order.update_price.value != '0.00'" class="info-item">
        <div class="item-lable">后台改价</div>
        <div class="item-content">
          <span>{{ order.update_price.symbol }}</span>
          <span>￥{{ order.update_price.value }}</span>
        </div>
      </div>
      <div class="divider"></div>
      <div class="trade-total">
        <span class="lable">实付款</span>
        <div class="goods-price">
          <span class="unit">￥</span>
          <span class="value">{{ order.pay_price }}</span>
        </div>
      </div>
    </div>

    <!-- 底部操作按钮 -->
    <div v-if="order.order_status != 20" class="footer-fixed">
      <div class="btn-wrapper">
        <!-- 未支付取消订单 -->
        <span v-if="order.pay_status == 10">
          <div class="btn-item" @click="toCancelOrder(order.order_id)">
            取消
          </div>
        </span>
        <!-- 已支付进行中的订单 -->
        <span v-if="order.order_status != 21">
          <span v-if="order.pay_status == 20 && order.delivery_status == 10">
            <div class="btn-item" @click="toCancelOrder(order.order_id)">
              申请取消
            </div>
          </span>
        </span>
        <!-- 已申请取消 -->
        <div v-else class="f-28 col-8">取消申请中</div>
        <!-- 未支付的订单 -->
        <span v-if="order.pay_status == 10">
          <div class="btn-item active" @click="isPayAction = true">去支付</div>
        </span>
        <!-- 确认收货 -->
        <span v-if="order.delivery_status == 20 && order.receipt_status == 10">
          <div
            class="btn-item active"
            @click="toConfirmReceipt(order.order_id)"
          >
            确认收货
          </div>
        </span>
        <!-- 订单评价 -->
        <span v-if="order.order_status == 30 && order.is_comment == 0">
          <div
            class="btn-item"
            @click="$router.push(`/addcomment/?orderId=${order.order_id}`)"
          >
            评价
          </div>
        </span>
      </div>
    </div>

    <!-- 支付方式弹窗 -->
    <PayAction
      :isPayAction="isPayAction"
      @closeSheet="handleClose"
      :orderData="orderData"
    ></PayAction>
  </div>
</template>

<script>
import { getOrderDetailApi } from '@/api/orderList'
import PayAction from '@/components/PayAction.vue'
import orderActions from '@/mixins/orderActions'
import { Toast } from 'vant'

export default {
  name: 'OrderDetail',
  mixins: [orderActions],
  components: {
    PayAction
  },

  data () {
    return {
      isPayAction: false,

      orderId: this.$route.query.orderId,
      order: {}

    }
  },

  computed: {
    // 传递给PayAction组件的数据
    orderData () {
      return {
        orderId: this.orderId
      }
    }
  },

  methods: {
    handleClose (boolean) {
      this.isPayAction = boolean
    },

    async getOrderDetail () {
      const { data: { order } } = await getOrderDetailApi(this.orderId)
      this.order = order
    },

    handleTargetExpress () {
      Toast('未设置物流查询端口')
    },

    handleCopy (content) {
      navigator.clipboard.writeText(content)
        .then(() => {
          Toast('已复制到剪切板')
        })
        .catch(() => {
        })
    }
  },

  created () {
    this.getOrderDetail()
  }
}
</script>

<style lang="less" scoped>
// 页面顶部
.page-body {
  background: #f4f4f4;
  padding: 48px 0;
}
.header {
  display: flex;
  justify-content: space-between;
  background-color: #e8c269;
  height: 140px;
  padding: 28px 15px 0 15px;

  .order-status {
    display: flex;
    align-items: center;
    height: 64px;

    .status-icon {
      width: 64px;
      height: 64px;

      .image {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .status-text {
      padding-left: 10px;
      color: #fff;
      font-size: 19px;
      font-weight: bold;
    }
  }

  .next-action {
    display: flex;
    align-items: center;
    height: 64px;

    .action-btn {
      min-width: 76px;
      height: 28px;
      padding: 0 15px;
      line-height: 28px;
      background-color: #fff;
      text-align: center;
      border-radius: 14px;
      border-color: rgb(102, 102, 102);
      cursor: pointer;
      user-select: none;
      color: #c7a157;
    }
  }
}

// 通栏卡片
.i-card {
  background: #fff;
  padding: 12px 12px;
  width: 94%;
  box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.05);
  margin: 0 auto 10px auto;
  border-radius: 10px;
}

// 收货地址
.delivery-address {
  margin-top: -25px;

  .link-man {
    line-height: 23px;
    color: #333;

    .name {
      margin-right: 5px;
    }
  }

  .address {
    margin-top: 6px;
    color: #999;
    font-size: 12px;

    .detail {
      margin-left: 3px;
    }
  }
}

// 物流公司
.express {
  display: flex;
  align-items: center;

  .main {
    flex: 1;
  }

  .info-item {
    display: flex;
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }

    .item-lable {
      display: flex;
      align-items: center;
      font-size: 12px;
      color: #999;
      margin-right: 15px;
    }

    .item-content {
      flex: 1;
      display: flex;
      align-items: center;
      font-size: 13px;
      color: #333;

      .act-copy {
        margin-left: 10px;
        padding: 1px 10px;
        font-size: 11px;
        color: #666;
        border: 1px solid #c1c1c1;
        border-radius: 8px;
      }
    }
  }

  // 右侧箭头
  .right-arrow {
    margin-left: 8px;
    // color: #777;
    font-size: 13px;
  }
}

// 商品列表
.goods-list {
  // 商品项
  .goods-item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    // 商品信息
    .goods-main {
      display: flex;
    }

    // 商品图片
    .goods-image {
      width: 90px;
      height: 90px;

      .image {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 4px;
      }
    }

    // 商品内容
    .goods-content {
      flex: 1;
      padding-left: 8px;
      padding-top: 8px;

      .goods-title {
        font-size: 13px;
        max-height: 38px;
      }

      .goods-props {
        margin-top: 7px;
        height: 20px;
        color: #ababab;
        font-size: 12px;
        overflow: hidden;

        .goods-props-item {
          display: inline-block;
          margin-right: 7px;
          padding: 2px 8px;
          border-radius: 6px;
          background-color: #f5f5f5;
          width: auto;
        }
      }
    }

    // 交易信息
    .goods-trade {
      padding-top: 8px;
      width: 75px;
      text-align: right;
      color: grey;
      font-size: 13x;

      .goods-price {
        vertical-align: bottom;
        margin-bottom: 8px;

        .unit {
          margin-right: -1px;
          font-size: 12px;
        }
      }
    }

    // 商品售后
    .goods-refund {
      display: flex;
      justify-content: flex-end;

      .stata-text {
        font-size: 12px;
        color: #999;
      }

      .action-btn {
        border-radius: 14px;
        padding: 4px 13px;
        font-size: 12px;
        color: #383838;
        border: 1px solid #a8a8a8;
      }
    }
  }
}

// 订单信息
.order-info {
  .info-item {
    display: flex;
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }

    .item-lable {
      display: flex;
      align-items: center;
      font-size: 12px;
      color: #999;
      margin-right: 15px;
    }

    .item-content {
      flex: 1;
      display: flex;
      align-items: center;
      font-size: 13px;
      color: #333;

      .act-copy {
        margin-left: 10px;
        padding: 1px 10px;
        font-size: 11px;
        color: #666;
        border: 1px solid #c1c1c1;
        border-radius: 8px;
      }
    }
  }
}

// 交易信息
.trade-info {
  .info-item {
    display: flex;
    margin-bottom: 12px;

    .item-lable {
      font-size: 12px;
      color: #999;
      margin-right: 12px;
    }

    .item-content {
      flex: 1;
      font-size: 13px;
      color: #333;
      text-align: right;
    }
  }

  .divider {
    height: 1px;
    background: #f1f1f1;
    margin-bottom: 12px;
  }

  .trade-total {
    display: flex;
    justify-content: flex-end;

    .goods-price {
      margin-left: 6px;
      vertical-align: bottom;

      .unit {
        margin-right: -1px;
        font-size: 12px;
      }
    }
  }
}

/* 底部操作栏 */

.footer-fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
  box-shadow: 0 -2px 20px 0 rgba(151, 151, 151, 0.24);
  background: #fff;

  .btn-wrapper {
    height: 53px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 15px;
  }

  .btn-item {
    min-width: 82px;
    border-radius: 14px;
    padding: 5px 12px;
    font-size: 14px;
    color: #383838;
    text-align: center;
    border: 1px solid #a8a8a8;
    margin-left: 12px;

    &.active {
      color: #fff;
      border: none;
      background: linear-gradient(to right, #f9211c, #ff6335);
    }
  }
}
</style>
